<template>
 <div class="reset-passwords-page">
    <div v-if="stepIndex !== 2" class="login-public reset-passwords">
     <p class="reset-passwords_header">重置密码</p>
     <div class="reset-passwords_steps">
         <el-steps :space="300" :active="stepIndex" finish-status="success">
            <el-step title="验证身份"></el-step>
            <el-step title="设置新密码"></el-step>
            <el-step title="重置成功"></el-step>
        </el-steps>
     </div>
      <transition name="el-fade-in">
        <div v-if="stepIndex === 0" class="reset-passwords_content">
            <el-form :model="form" status-icon :rules="rules" ref="form" label-width="100px" class="login-ruleForm">
                    <el-form-item  prop="userAccount">
                        <el-input  v-model="form.userAccount" autocomplete="new-password" placeholder="请输入账号">
                            <template slot="prepend"><SvgIcon icon-class="iconzhanghao" size="24px"></SvgIcon></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item  prop="authCode">
                        <el-input type="password" v-model="form.authCode"  @keyup.enter.native="setUserAccount('form')" autocomplete="new-password" placeholder="请输入邮箱验证码">
                            <template slot="prepend"><SvgIcon icon-class="iconyouxiang" size="24px"></SvgIcon></template>
                            <template slot="suffix"><span @click="sendVerificationCode" v-if="verificationCode" class="auth-code">{{codeTitle}}</span><label v-else class="count-down">{{sendCode}}秒后可重发</label></template>
                        </el-input>
                    </el-form-item>
                <el-form-item>
                    <el-button class="login-button" type="primary" size="medium" @click="setUserAccount('form')">下一步</el-button>
                </el-form-item>
                </el-form>
           </div>
            <div v-if="stepIndex === 1" class="reset-passwords_content">
                <el-form :model="formPassword" status-icon :rules="rules" ref="formPassword" label-width="100px" class="login-ruleForm">
                        <el-form-item  prop="newPassword">
                            <el-input  v-model="formPassword.newPassword" autocomplete="new-password" placeholder="设置设置6至20位登录密码">
                                <template slot="prepend"><SvgIcon icon-class="iconxinmima" size="24px"></SvgIcon></template>
                            </el-input>
                        </el-form-item>
                        <el-form-item  prop="confirmPassword">
                            <el-input type="password" v-model="formPassword.confirmPassword" @keyup.enter.native="setNewPassword('formPassword')" autocomplete="new-password" placeholder="请再次输入登录密码">
                                <template slot="prepend"><SvgIcon icon-class="iconxinmima" size="24px"></SvgIcon></template>
                            </el-input>
                        </el-form-item>
                    <el-form-item>
                        <el-button class="login-button" type="primary" size="medium" @click="setNewPassword('formPassword')">下一步</el-button>
                    </el-form-item>
                </el-form>
            </div>
     </transition>
   </div>
    <transition name="el-fade-in">
        <div v-if="stepIndex === 2" class="reset-success">
                <div class="reset-success_content">
                    <i class="el-icon-success"></i>
                    <p>密码重置成功</p>
                    <p>请妥善保管您的账户信息</p>
                    <el-button @click="restartLogin">重新登录</el-button>
                </div>
        </div>
    </transition>
 </div>
</template>

<script>
import loginRequest from '@/api/login'
export default {
 name:'resetPasswords',
 data() {
    const checkNewPassword = (rule, value, callback) => {
      const  reg = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,20}$/
        if (!value) {
            callback(new Error('设置设置6至20位登录密码'))
        } 
        if(!reg.test(value)){
            callback(new Error('密码6至20位，必须包含字母与数字'))
        }else{
            callback()
        }
    };
    const checkConfirmPassword = (rule, value, callback) => {
        if (!value) {
            callback(new Error('请再次输入登录密码'))
        }
        if(value === this.formPassword.newPassword){
            callback()
        }else{
            callback(new Error('两次密码不一致')) 
        }
    };
    return {
       stepIndex:0,
       verificationCode:true,
       sendCode:60,
       codeTitle:'发送验证码',
       form: {
          userAccount:'',
          authCode: ''
        },
        formPassword:{  
          newPassword:'',
          confirmPassword:''
        },
        rules: {
          userAccount: [
            { required: true, message: '请输入账号', trigger: 'blur' }
          ],
          authCode: [
            { required: true, message: '请输入邮箱验证码', trigger: 'blur' }
          ],
          newPassword: [
            { required: true,  validator: checkNewPassword, trigger: 'blur' }
          ],
          confirmPassword: [
            { required: true,  validator: checkConfirmPassword, trigger: 'blur' }
          ]
        }    
    }
 },
 methods:{
     sendVerificationCode(){
        if(!this.form.userAccount){
               this.$notify({
                    title: 'warning',
                    message: '请输入账号',
                    type: 'warning'
                });
            return 
        }

        if(this.sendCode === 60){
            this.sendSecurityCode(this.form.userAccount)
        }               
     },
     //倒计时
     countDown(){
        const self=this
        setTimeout(function cb(){
            self.codeTitle=`${self.sendCode}秒后可重发`
             if(self.sendCode === 0){
                 self.verificationCode=true
                 self.sendCode=60
                 self.codeTitle='重新发送验证码'
             }else{              
                self.sendCode--
                setTimeout(cb, 1000)
             }
         },1000)
     },
     setUserAccount(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.validateSecurityCode()           
          } else {
            return false;
          }
        });
      },
      setNewPassword(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
             const obj={
                    securityCode:this.form.authCode,
                    userName:this.form.userAccount,
                    newPassword:this.formPassword.newPassword
             }
             this.resetPassword(obj)
           
          } else {
            return false;
          }
        });
      },
      //发送验证码
      sendSecurityCode(name){
        const url=loginRequest.sendSecurityCode({
            userName:name,
        })
        this.$get(url)
        .then( res => {
                if(res.status === 1){
                    this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                    this.countDown()
                   
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                }) 
                }               
        })
    },
    //校验验证码
    validateSecurityCode(){
        const url=loginRequest.validateSecurityCode({
            userName:this.form.userAccount,
            securityCode:this.form.authCode
        })
        this.$get(url)
        .then( res => {
                if(res.status === 1){
                    this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                    this.stepIndex=1
                    this.$nextTick(()=>{
                        this.$refs['formPassword'].resetFields();
                    })
                   
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                }) 
                }               
        })
    },
    //重置密码
    resetPassword(formData){
         const url=loginRequest.resetPassword
        this.$put(url,formData)
        .then( res => {
                if(res.status === 1){
                    this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                    this.stepIndex=2
                   
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                }) 
                }               
        })
    },
      restartLogin(){
          this.$router.replace({
              path:'/'
          })
      }
 }
}
</script>

<style lang="scss">
 @import '../../styles/login.scss'
</style>



